.flex-wrap{
    display: flex;
    align-items: flex-start;
    justify-content: space-evenly;
    margin: 2rem;
    color: black;
}
.rect-rotating-text-cursor{
    animation: blinking-cursor 1s cubic-bezier(0.58,0.01,0.01,0.99) 0s infinite;
}
.text-rotate{
    /* font-size: 29px; */
    color: red;
}
@keyframes blinking-cursor {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
@keyframes fadeUp {
    from {
      transform: translateY(-50px);
      opacity: 0;
    } to {
      transform: translateY(0);
      opacity: 1;
    }
}
/* RESPONSIVE */
@media(max-width: 768px){
    .flex-wrap{
        display: flex;
        flex-direction: column;
    }
}
@media(max-width: 576px){
    .flex-wrap{
        display: flex;
        flex-direction: column;
    }
    .flex-wrap .left h2{
        margin-top: -10px;
    }
    .flex-wrap .left h1{
        font-size: 25px;
    }
    .flex-wrap .right img{
        width: 300px;
        margin-top: 50px;
        margin-left: 0px;
    }
}