.row-header{
    margin:0px auto;
    padding:0px auto;
}
.row-content {
    margin:0px auto;
    padding: 50px 0px 50px 0px;
    border-bottom: 1px ridge;
    min-height:400px;
}
.footer{
    background-color: #dfc2d6;
    margin:0px auto;
    padding: 20px 0px 20px 0px;
}
.jumbotron {
    padding:70px 30px 70px 30px;
    margin:0px auto;
    background: #c382e9 ;
    color:rgb(10, 9, 8);
}
address{
    font-size:80%;
    margin:0px;
    color:#0f0f0f;
}
.navbar-dark {
    background-color: black;
}
.page-enter{
    opacity: 0.01;
    transform: translateX(-100%);
}
.page-enter-active{
    opacity: 1;
    transform: translateX(0%);
    transition: all 300ms ease-in;
}
.page-exit{
    opacity: 1;
    transform: translateX(0);
}
.page-exit-active{
    opacity: 0.01;
    transform: translateX(100%);
    transition: all 300ms ease-out;
}
.App{
    animation: fadeUp 4000ms 0ms forwards;
}
@keyframes fadeUp{
    from{
        transform: translateY(-50px);
        opacity: 0;
    }
    to{
        transform: translateY(0);
        opacity: 1;
    }
}