.loading{
    height: 100vh;
    background: rgb(216, 40, 40);
    color: rgb(22, 22, 21);
    display: grid;
    align-items: center;
    text-align: center;
}
.loader{
    border: 5px solid #10b2c7;
    border-top: 5px solid rgb(182, 226, 22);
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin linear 2s infinite;
    position: absolute;
    top: 33%;
    left: 48%;
}
@keyframes spin{
    0% {transform: rotate(0deg);}
    100% {transform: rotate(360deg);}
}
@media(max-width: 576px){
    .loader{
        left: 45%;
    }
}