.abt-wrap{
    padding-left: 7rem;
    animation: fadeUp 1100ms 0ms forwards;
}
@keyframes fadeUp{
    from{
        transform: translateY(-50px);
        opacity: 0;
    }
    to{
        transform: translateY(0);
        opacity: 1;
    }
}
.abt-wrap .first{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}
.abt-wrap .first .abt-left{
    width: 800px;
    margin-right: 100px;
}
.abt-wrap .first .abt-right img{
    width: 210px;
    border-radius: 50%;
}
.abt-left p{
    font-size: 21px;
    text-align: justify;
    margin-bottom: 32px;
}
.abt-btn-wrap{
    display: flex;
}
.abt-btn-1{
    padding: 11px 26px;
    border: 1px solid red;
    border-radius: 50px;
    background: rosybrown;
    color: black;
    margin-right: 15px;
    font-size: 18px;
    cursor: pointer;
    transition: 0.5s ease;
}
.abt-btn-1:hover{
    background: black;
    color: blanchedalmond;
}
.abt-icon-wrap a{
    color: black;
    margin-right: 20px;
}
.abt-icon-wrap a:hover{
    color: red;
}
.third{
    display: flex;
    align-items: center;
}
.third .abt-right{
    margin-right: 70px;
}
.third .abt-right img{
    margin-top: 1.2rem;
    border-radius: 25%;
}
.third .abt-left{
    margin-top: 1.5rem;
    width: 750px;
}

/*RESPONSIVE FOR WIDTH OF 576px*/
@media(max-width: 576px)    {
    .third{
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
    }
    .third .abt-left{
        width: 270px;
    }
    .third .abt-left p{
        text-align: center;
    }
    .abt-wrap{
        padding-left: 1rem;
    }
    .abt-wrap .first{
        display: flex;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: flex-start;
    }
    .abt-wrap .first .abt-left{
        width: 270px;
    }
    .abt-wrap .first .abt-left p {
        text-align: left;
    }
    .abt-btn-wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 54%;
    }
    .abt-btn-1{
        margin-bottom: 15px;
    }
    .abt-icon-wrap{
        margin-bottom: 1rem;
    }
    
}