.containerTimeline{
    padding-top: 10%;
    padding-bottom: 5%;
    background: chocolate;
}

.eduHistoryHeader{
    text-align: right;
    padding-bottom: 2%;
    font-weight: 600;
    color: black;
}
section#timeline{
    width: 80%;
    margin: 20px auto;
    position: relative;
    margin: 30px;
    margin-left: 60px;
    animation: fadeUp 1100ms 0ms forwards;
}
@keyframes fadeUp{
    from{
        transform: translateY(-50px);
        opacity: 0;
    }
    to{
        transform: translateY(0);
        opacity: 1;
    }
}
section#timeline:before{
    content: '';
    display: block;
    position: absolute;
    left: 50%;
    top: 0;
    margin: 0 0 0 -1px;
    width: 2px;
    height: 100%;
    background: lightgray;
}
section#timeline article{
    width: 100%;
    margin: 0 0 20px 0;
    position: relative;
}
section#timeline article:after{
    content: '';
    display: block;
    clear: both;
}
section#timeline article div.inner{
    width: 40%;
    float: left;
    margin: 1px 0 0 0;
    border-radius: 6px;
}
section#timeline article div.inner span.date{
    display: block;
    width: 60px;
    height: 50px;
    padding: 5px 0;
    position: absolute;
    top: 0;
    left: 50%;
    margin: 0 0 0 -32px;
    border-radius: 100%;
    font-size: 12px;
    font-weight: 900;
    background: #25303B;
    color: rgba(255,255,255,0.5);
    border: 2px solid rgba(255,255,255,0.2);
    box-shadow: 0 0 0 7px #25303B;
    text-align: center;
}
i{
    margin-top: 7px;
}
section#timeline article div.inner span.date span {
    display: block;
    text-align: center;
}
section#timeline article div.inner span.date span.day {
    font-size: 10px;
}
section#timeline article div.inner span.date span.month {
    font-size: 18px;
}
section#timeline article div.inner span.date span.year {
    font-size: 10px;
}
h3{
    margin-top: -1px;
}
section#timeline article div.inner h2{
    padding: 15px;
    margin: 0;
    color: rgb(142, 178, 178);
    font-size: 20px;
    letter-spacing: -1px;
    border-radius: 6px 6px 0 0;
    position: relative;
}
section#timeline article div.inner h2:after {
    content: '';
    position: absolute;
    top: 20px;
    right: -5px;
        width: 10px; 
        height: 10px;
  }
  section#timeline article div.inner p {
    padding: 15px;
    margin: 0;
    font-size: 14px;
    background: #fff;
    color: #656565;
    border-radius: 0 0 6px 6px;
  }
  section#timeline article:nth-child(2n+2) div.inner {
    float: right;
  }
  section#timeline article:nth-child(2n+2) div.inner h2:after {
    left: -5px;
  }
  section#timeline article:nth-child(1) div.inner h2 {
    background: #141d22;
  }
  section#timeline article:nth-child(1) div.inner h2:after {
    background: #141d22;
  }
  section#timeline article:nth-child(2) div.inner h2 {
    background: #141d22;
  }
  section#timeline article:nth-child(2) div.inner h2:after {
    background: #141d22;
  }
  section#timeline article:nth-child(3) div.inner h2 {
    background: #141d22;
  }
  section#timeline article:nth-child(3) div.inner h2:after {
    background: #141d22;
  }
  section#timeline article:nth-child(4) div.inner h2 {
    background: #1abc9c;
  }
  section#timeline article:nth-child(4) div.inner h2:after {
    background: #1abc9c;
  }
  section#timeline article:nth-child(5) div.inner h2 {
    background: #9b59b6;
  }
  section#timeline article:nth-child(5) div.inner h2:after {
    background: #9b59b6;
  }
@media only screen and (max-width: 767px){
    section#timeline:before{
        left: 10%;
    }
    section#timeline article div.inner span.date{
        left: 10%;
    }
    section#timeline article div.inner{
        width: 70%;
    }
    section#timeline article:nth-child(n) div.inner {
        float: right;
       }
    section#timeline article:nth-child(n) div.inner h2:before {
         background: #9b59b6;
    }
    section#timeline article:nth-child(n) div.inner h2:after {
         display: none;
    }
    .containerTimeline{
        padding-top: 20%;
    }
}
@media only screen and (min-width: 768px){
    .innerInner{
        transition: transform 0.2s;
    }
    .innerInner:hover{
        transform:  scale(1.1);
        z-index: 500;
    }
    
}